.footer 
  background-color: $footer-bg
  padding-top: 72px 
  padding-bottom: 112px
  display: flex
  justify-content: center

  &__inner
    max-width: 1200px
    width: 100%
    padding: 0 30px
    box-sizing: border-box

  &__top
    width: 100%
    display: flex
    justify-content: flex-start
    padding-bottom: 45px
    gap: 90px

  &__bottom
    padding-top: 47px
    border-top: 2px solid $white
    width: 100%
    display: flex
    justify-content: space-between
    align-items: center


  &__links
    list-style-type: none
    display: flex
    flex-direction: column
    gap: 18px
    padding-left: 0
    margin: 0

  &__link
    font-family: $helb
    font-weight: 700
    text-decoration: none
    color: $text-footer
    font-size: 24px
    line-height: 1.3
    letter-spacing: 2px
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        color: $text-hover

    &:active
      color: $text-hover

  &__socials
    list-style-type: none
    display: flex
    flex-direction: column
    gap: 25px
    padding-left: 0
    margin: 0

  &__social-wrapper
    line-height: 1


  &__social-link
    font-family: $tit
    color: $text-footer
    font-size: 16px
    font-weight: 600
    line-height: 1.5
    letter-spacing: 2.14px
    text-transform: uppercase
    text-decoration: none
    transition: all 0.3s ease

    &:hover
      @media #{$mouse-device}
        color: $text-hover

    &:active
      color: $text-hover

  &__copyright
    font-family: $tit
    color: $text-footer
    font-size: 14px
    margin: 0



@media (max-width: 1200px)
  .footer
    padding-top: 40px
    padding-bottom: 40px


@media (max-width: 480px)
  .footer
    &__inner
      padding-left: 15px
      padding-right: 15px

    &__top
      gap: 30px

    &__bottom
      flex-direction: column
      gap: 30px